/* Контейнер для страницы */
.container-contact {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

/* Раздел для формы */
.contact-page .contact-section {
  width: 100%;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  padding: 8px;
  margin-top: 5px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.form-group button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 15px;
}

.form-group button:hover {
  background-color: #0056b3;
}

/* Боковая панель с новостями */
.sidebar {
  width: 30%;
  background-color: #f4f4f4;
  padding: 20px;
  border-radius: 4px;
}

.sidebar h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.sidebar ul {
  list-style: none;
  padding-left: 0;
}

.sidebar li {
  margin-bottom: 10px;
}

.sidebar button {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  text-align: left;
  width: 100%;
}

.sidebar button:hover {
  text-decoration: underline;
}

/* Модальное окно */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 600px;
  width: 100%;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}



