.shortcut-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.shortcut-card {
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background: #f9f9f9;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: 0.3s;
}

.shortcut-card:hover {
  transform: scale(1.05);
}

.shortcut-icon {
  font-size: 40px;
  color: #007bff;
}

.shortcut-info h3 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
}

.shortcut-info p {
  margin: 5px 0;
  color: #555;
}

h2 {
  margin-top: 30px;
  font-size: 24px;
  font-weight: bold;
}

.container {
  margin: 20px;
}
