.header {
  background: #f4f4f4;
  padding: 15px 40px;
  border-bottom: 1px solid #ddd;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0;
  cursor: default;
}

.menu {
  display: flex;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu li {
    margin: 0;
}

.menu a {
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}
