.tabs {
  display: flex;
  gap: 10px;
  margin: 15px 0;
}

.tabs button {
  padding: 10px 15px;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tabs .active {
  color: white;
}

.plugin-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.plugin-card {
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.plugin-icon {
  font-size: 30px;
  margin-bottom: 10px;
}

.install-button {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 8px 12px;
  background: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  margin-top: 10px;
}

.install-button:hover {
  background: #0056b3;
}
